`use strict`;

// Напиши скрипт, который после нажатия кнопки Start,
// раз в секунду меняет цвет фона body на случайное
// значение из массива используя инлайн-стиль.
// При нажатии на кнопку Stop, изменение цвета фона
// должно останавливаться.

//  Учти, на кнопку Start можно нажать бесконечное
// количество раз. Сделай так, чтобы пока изменение
// темы запушено, кнопка Start была не активна.

// Для генерации случайного числа (индекс элемента
// массива цветов), используй
// функцию randomIntegerFromInterval

const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};
const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];
const refs = {
  start: document.querySelector('[data-action="start"]'),
  stop: document.querySelector('[data-action="stop"]'),
  body: document.querySelector('body'),
};

let bodyColor = colors => {
  return colors[randomIntegerFromInterval(0, colors.length - 1)];
};
let changeColor;
const startClick = () => {
  changeColor = setInterval(() => {
    refs.body.style.backgroundColor = bodyColor(colors);
  }, 1000);
  refs.stop.removeAttribute('disabled');
  refs.start.setAttribute('disabled', 'true');
};
const stopClick = () => {
  clearInterval(changeColor);
  refs.stop.setAttribute('disabled', 'true');
  refs.start.removeAttribute('disabled');
  refs.body.style.backgroundColor = '#FFFFFF';
};

refs.start.addEventListener('click', startClick);
refs.stop.addEventListener('click', stopClick);
